import type { ReactNode } from "react";

interface WrapperProps {
  children: ReactNode;
}

function Wrapper({ children }: WrapperProps) {
  return <div>{children}</div>;
}

interface ContentProps {
  children: ReactNode;
}

function Content({ children }: ContentProps) {
  return <div>{children}</div>;
}

export const Header = {
  Wrapper,
  Content,
};
